.first-row {
  margin-top: 10px;
  display: grid;
  grid-template-columns: 20% 59% 21%;
}

.second-row {
  display: grid;
  grid-template-columns: 15% 85%;
  grid-template-rows: auto;
}

.third-row {
  display: grid;
  grid-template-columns: 28% 41% 31%;
  margin-bottom: 3px;
}

.SKIP-BACKWARD-15 {
  justify-self: end;
}

.SPEED-SLIDER {
  justify-self: center;
}

#speed-slider {
  vertical-align: top;
}


/* Speed slider styling */

@media screen and (-webkit-min-device-pixel-ratio:0) {
  input[type='range'] {
    overflow: hidden;
    width: 88px;
    -webkit-appearance: none;
    background-color: rgb(88, 42, 114);
    cursor: pointer;
    outline: 0;
  }
  
  input[type='range']::-webkit-slider-runnable-track {
    height: 10px;
    -webkit-appearance: none;
    margin-top: -1px;
    outline: 0;
  }
  
  input[type='range']::-webkit-slider-thumb {
    width: 10px;
    -webkit-appearance: none;
    height: 10px;
    cursor: pointer;
    background: #000000;
    outline: 0;
    box-shadow: 10px 10px;
    
  }

}



